const { Text, Link } = Typography;

const WebSocketComponent = () => {
  const [logs, setLogs] = useState<string[]>([]);
  const { ip, port } = useWebSocketConfig();

  useEffect(() => {
    if (ip && port && wsService.getStatus() !== 'open') {
      wsService.connect(`ws://${ip}:${port}`);
    }
  }, [ip, port]);

  return (
    <Card title="串口日志">
      <pre style={{ maxHeight: 400, overflow: 'auto', margin: 0 }}>
        {logs.join('\n')}
      </pre>
    </Card>
  );
};

export default () => {
  const { ip, port } = useWebSocketConfig();
  const { setInitialState } = useModel('@@initialState');
  
  // 使用滚动重置Hook
  useScrollReset();
  
  const [showImeiEdit, setShowImeiEdit] = useState(false);
  const [imeiClickCount, setImeiClickCount] = useState(0);
  const [imei, setImei] = useState('');
  const [group, setGroup] = useState('Quectel_AT');

  // 主动连接WebSocket并检测命令族
  useEffect(() => {
    if (ip && port && wsService.getStatus() !== 'open') {
      wsService.connect(`ws://${ip}:${port}`);
    }
    const setTitle = async () => {
      await ensureGroupReady(ip, port);
      const atiRaw = await safeSendAT('ATI');
      const { model } = parseATISystemInfo(atiRaw);
      setInitialState((prev: any) => ({
        ...prev,
        dynamicTitle: model ? `${model}` : 'Modem-Manager',
      }));
    };
    setTitle();
  }, [ip, port]);

  // 监听SystemInfoCard数据变化，自动更新IMEI和group
  // 这里假设SystemInfoCard可以通过ref或回调获取info和group，或你可以在fetchSystemInfo后setImei/setGroup
  // 这里用简单方案：每次点击IMEI时都setImei(info?.imei)
  const handleImeiClick = (currentImei: string) => {
    setImeiClickCount(c => {
      if (c + 1 >= 5) {
        setShowImeiEdit(true);
        setImei(currentImei);
        return 0;
      }
      return c + 1;
    });
  };
  const handleImeiEditSuccess = () => {
    setShowImeiEdit(false);
    // 这里可以触发系统信息刷新
    // ...
  };

  return (
    <Row gutter={[16, 16]} align="stretch">
      <Col xs={24} md={12}>
        <ATServerConfig />
      </Col>
      <Col xs={24} md={12}>
        <SystemInfoCard onImeiClick={() => handleImeiClick(imei)} />
      </Col>
      {showImeiEdit && (
        <Col xs={24}>
          <ImeiEditCard imei={imei} onSuccess={handleImeiEditSuccess} onHide={() => setShowImeiEdit(false)} />
        </Col>
      )}
      <Col xs={24} md={12}>
        <SimCardConfigCard />
      </Col>
      <Col xs={24} md={12}>
        <DeviceControlCard />
      </Col>
      <Col xs={24} md={24}>
        <NetSystemConfigCard />
      </Col>
    </Row>
  );
};

const ATServerConfig = () => {
  const { ip, port, setConfig, isConfigLocked } = useWebSocketConfig();
  const [inputIp, setInputIp] = useState(ip);
  const [inputPort, setInputPort] = useState(port);
  const { message } = App.useApp();

  // 关键：同步 context 变化到输入框
  useEffect(() => {
    setInputIp(ip);
    setInputPort(port);
  }, [ip, port]);

  return (
    <Card
      title="AT服务器配置"
      className="my-card"
    >
      <div style={{ marginBottom: 16 }}>
        <div className="my-title">当前服务模式:
          <Link style={{ color: 'var(--ant-color-primary)', fontWeight: 'bold' }}>串口AT</Link>
          <ReloadOutlined style={{ color: 'var(--ant-color-primary)', marginLeft: 8, cursor: 'pointer' }} />
        </div>
        <div className="my-tip">通过网络连接与设备通信，支持远程控制</div>
      </div>
      <Input
        value={inputIp}
        onChange={e => setInputIp(e.target.value)}
        style={{ marginBottom: 8 }}
        placeholder="IP地址"
        disabled={isConfigLocked}
      />
      <Input
        value={inputPort}
        onChange={e => setInputPort(e.target.value)}
        style={{ marginBottom: 8 }}
        placeholder="端口"
        disabled={isConfigLocked}
      />
      <Button
        type="primary"
        block
        size="large"
        className="my-btn"
        onClick={async () => {
          resetGroupReady();
          if (wsService.getStatus && wsService.getStatus() === 'open') {
            wsService.close && wsService.close();
            setTimeout(() => {
              setConfig(inputIp, inputPort);
              wsService.connect && wsService.connect(`ws://${inputIp}:${inputPort}`);
            }, 300);
          } else {
            setConfig(inputIp, inputPort);
            wsService.connect && wsService.connect(`ws://${inputIp}:${inputPort}`);
          }
          message.success('AT服务器配置已保存');
        }}
        disabled={isConfigLocked}
      >
        保存
      </Button>
      {isConfigLocked && (
        <div style={{ color: 'red', marginTop: 8, marginBottom: 4, fontSize: 12 }}>
          config.json配置已启用，当前配置被锁定
        </div>
      )}
      <div className="my-tip">
        支持 IPv4（例如：192.168.1.1）和 IPv6（例如：2001:db8::1）地址
      </div>
    </Card>
  );
};

const SystemInfoCard = ({ onImeiClick }: { onImeiClick: () => void }) => {
  const { ip, port } = useWebSocketConfig();
  const [info, setInfo] = useState<SystemInfo | null>(null);
  const [loading, setLoading] = useState(false);
  // 记录已识别的命令族
  const [commandGroup, setCommandGroup] = useState<string>(() => getRememberedCommandGroup(ip, port) || 'Fibocom_AT');
  const [model, setModel] = useState<string>('');
  // IMEI修改相关
  const [showImeiEdit, setShowImeiEdit] = useState(false);
  const [imeiClickCount, setImeiClickCount] = useState(0);
  const [imeiInput, setImeiInput] = useState('');
  const [imeiEditLoading, setImeiEditLoading] = useState(false);
  const { message } = App.useApp();

  // const group = commandGroup;

  // 新的系统信息获取逻辑：
  async function fetchSystemInfo() {
    setLoading(true);
    // 1. 等待group识别好
    await ensureGroupReady(ip, port);
    const realGroup = await getCurrentGroupAsync(ip, port);
    let atiRaw = await safeSendAT('ATI');
    const parsed = parseATISystemInfo(atiRaw);
    setModel(parsed.model);
    setCommandGroup(realGroup);
    setRememberedCommandGroup(ip, port, realGroup);
    const cmds = getSystemInfoCmds(realGroup);
    let raws = [atiRaw];
    if (cmds.length > 1) {
      for (let i = 1; i < cmds.length; i++) {
        const raw = await safeSendAT(cmds[i]);
        raws.push(raw);
      }
    }

    const info = parseSystemInfoMulti(raws, realGroup);
    setInfo(info);
    setLoading(false);
  }

  useEffect(() => {
    let cancel = false;
    const doQuery = () => { if (!cancel) fetchSystemInfo(); };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ip, port]);

  // IMEI点击计数逻辑
  useEffect(() => {
    if (imeiClickCount >= 5) {
      setShowImeiEdit(true);
      setImeiClickCount(0);
    }
  }, [imeiClickCount]);

  const handleImeiClick = () => {
    setImeiClickCount(c => c + 1);
  };
  const handleImeiEdit = async () => {
    if (!/^\d{15}$/.test(imeiInput)) {
      message.warning('请输入15位数字IMEI');
      return;
    }
    setImeiEditLoading(true);
    try {
      // 假设atModule.ts已实现setImeiCmd
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setImeiCmd(realGroup, imeiInput));
      message.success('IMEI修改命令已发送');
      setShowImeiEdit(false);
      setImeiInput('');
      // 修改后刷新系统信息
      setTimeout(() => fetchSystemInfo(), 1000);
    } catch (e) {
      message.error('IMEI修改失败: ' + e);
    } finally {
      setImeiEditLoading(false);
    }
  };

  return (
    <Card
      title="系统信息"
      extra={<Button className="my-btn" size="small" loading={loading} onClick={() => fetchSystemInfo()} icon={<ReloadOutlined />}>刷新</Button>}
      className="my-card"
    >
      <Descriptions
        bordered
        column={1}
        styles={{ label: { width: '40%' } }}
      >
        <Descriptions.Item label="制造商">{info?.manufacturer || '-'}</Descriptions.Item>
        <Descriptions.Item label="设备型号">{info?.model || '-'}</Descriptions.Item>
        <Descriptions.Item label="固件版本">{info?.firmware || '-'}</Descriptions.Item>
        <Descriptions.Item label="IMEI">
          <span
            style={{ cursor: 'pointer', userSelect: 'text' }}
            onClick={onImeiClick}
          >
            {info?.imei || '-'}
          </span>
        </Descriptions.Item>
      </Descriptions>
    </Card>
  );
};

const ImeiEditCard = ({ imei, onSuccess, onHide }: { imei: string; onSuccess: () => void; onHide: () => void }) => {
  const { ip, port } = useWebSocketConfig();
  const [imeiInput, setImeiInput] = useState(imei || '');
  const [loading, setLoading] = useState(false);
  const { message } = App.useApp();

  const handleImeiEdit = async () => {
    if (!/^\d{15}$/.test(imeiInput)) {
      message.warning('请输入15位数字IMEI');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setImeiCmd(realGroup, imeiInput));
      message.success('IMEI修改命令已发送');
      setImeiInput('');
      onSuccess();
    } catch (e) {
      message.error('IMEI修改失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  return (
    <Card
      className="my-card"
      style={{ border: '1px solid var(--ant-color-primary-border)', background: 'var(--ant-color-info-bg)' }}
      title={<span style={{ color: 'var(--ant-color-primary)', fontWeight: 600 }}>IMEI修改</span>}
      extra={<Button type="link" onClick={onHide} danger>隐藏</Button>}
    >
      <div style={{ marginBottom: 16, background: 'var(--ant-color-warning-bg)', border: '1px solid var(--ant-color-warning-border)', color: 'var(--ant-color-warning)', borderRadius: 4, padding: 12 }}>
        <b>警告</b><br />
        修改IMEI是一个高风险操作，可能违反相关法律法规并导致设备无法正常使用。请确保您完全了解此操作的风险。
      </div>
      <Input
        placeholder="请输入新的IMEI（15位数字）"
        value={imeiInput}
        onChange={e => setImeiInput(e.target.value)}
        maxLength={15}
        style={{ width: '40%', marginRight: 16 }}
        disabled={loading}
      />
      <Button
        type="primary"
        danger
        loading={loading}
        onClick={handleImeiEdit}
      >修改IMEI</Button>
    </Card>
  );
};

const SimCardConfigCard = () => {
  const { ip, port } = useWebSocketConfig();
  const [simType, setSimType] = useState<'outer' | 'inner'>('outer');
  const [hotSwap, setHotSwap] = useState(true);
  const [pinEnabled, setPinEnabled] = useState(false);
  const [airplane, setAirplane] = useState(false);
  const [loading, setLoading] = useState(false);
  const [pinModal, setPinModal] = useState(false);
  const [pinMode, setPinMode] = useState<'enable' | 'disable'>('enable');
  const [pin1, setPin1] = useState('');
  const [pin2, setPin2] = useState('');
  const [pinError, setPinError] = useState('');
  const { message } = App.useApp();

  // 获取命令族
  // const group = getRememberedCommandGroup(ip, port) || 'Quectel_AT';

  // 判断WebSocket是否已连接
  function isWsReady() {
    return wsService.getStatus && wsService.getStatus() === 'open';
  }

  // 查询所有状态
  const queryAll = async () => {
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const simMsg = await safeSendAT(getSimSlotCmd(realGroup));
      const slot = parseSimSlot(simMsg, realGroup);
      if (slot) setSimType(slot);
      const hotMsg = await safeSendAT(getHotSwapCmd(realGroup));
      const hot = parseHotSwap(hotMsg, realGroup);
      if (typeof hot === 'boolean') setHotSwap(hot);
      const airMsg = await safeSendAT(getAirplaneCmd(realGroup));
      const air = parseAirplane(airMsg, realGroup);
      if (typeof air === 'boolean') setAirplane(air);
      const pinMsg = await safeSendAT(getPinStatusCmd(realGroup));
      const pin = parsePinStatus(pinMsg, realGroup);
      if (typeof pin === 'boolean') setPinEnabled(pin);
    } catch (e) {
      message.error('AT命令查询失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    let cancel = false;
    const doQuery = () => { if (!cancel) queryAll(); };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ip, port]);

  // 切换SIM卡槽
  const handleSimTypeChange = async (type: 'outer' | 'inner') => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setSimSlotCmd(realGroup, type));
      await queryAll();
    } catch (e) {
      message.error('切换SIM卡失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // 切换热插拔
  const handleHotSwap = async (enable: boolean) => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setHotSwapCmd(realGroup, enable));
      await queryAll();
    } catch (e) {
      message.error('设置热插拔失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // 切换飞行模式
  const handleAirplane = async (enable: boolean) => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setAirplaneCmd(realGroup, enable));
      await queryAll();
    } catch (e) {
      message.error('设置飞行模式失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // PIN码弹窗
  const showPinModal = (mode: 'enable' | 'disable') => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setPinMode(mode);
    setPin1('');
    setPin2('');
    setPinError('');
    setPinModal(true);
  };
  const handlePinOk = async () => {
    if (!/^\d{4,8}$/.test(pin1)) {
      setPinError('PIN码必须为4-8位数字');
      return;
    }
    if (pinMode === 'enable' && pin1 !== pin2) {
      setPinError('两次输入的PIN码不一致');
      return;
    }
    setPinError('');
    if (!isWsReady()) {
      setPinError('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      if (pinMode === 'enable') {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(setPinEnableCmd(realGroup, pin1));
      } else {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(setPinDisableCmd(realGroup, pin1));
      }
      setPinModal(false);
      await queryAll();
    } catch (e) {
      setPinError('操作失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card title="SIM卡配置">
      <Descriptions
        bordered
        column={1}
        styles={{ label: { width: '20%' }, content: { fontSize: 14 } }}
        className="my-card"
      >
        <Descriptions.Item label="切换">
          <div>
            <Space.Compact style={{ width: '100%' }}>
              <Button
                type={simType === 'outer' ? 'primary' : 'default'}
                onClick={() => handleSimTypeChange('outer')}
                style={{ flex: 1 }}
                loading={loading}
              >
                外置SIM卡
              </Button>
              <Button
                type={simType === 'inner' ? 'primary' : 'default'}
                onClick={() => handleSimTypeChange('inner')}
                style={{ flex: 1 }}
                loading={loading}
              >
                内置SIM卡
              </Button>
            </Space.Compact>
            <div className="my-tip">
              切换SIM卡需要重启设备，请确保没有重要的网络操作正在进行
            </div>
          </div>
        </Descriptions.Item>
        <Descriptions.Item label="热插拔">
          <div>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <Switch
                checked={hotSwap}
                loading={loading}
                onChange={handleHotSwap}
                style={{ marginRight: 8 }}
              />
              <span style={{ color: hotSwap ? 'var(--ant-color-primary)' : 'var(--ant-color-text-secondary)', marginRight: 16 }}>{hotSwap ? '已开启' : '已关闭'}</span>
            </div>
            <div className="my-tip">
              开启后可以在设备运行时插拔外置SIM卡
            </div>
          </div>
        </Descriptions.Item>
        <Descriptions.Item label="PIN码管理">
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <span style={{ marginRight: 0 }}>状态:</span>
              <span
                style={{
                  color: pinEnabled ? 'var(--ant-color-primary)' : 'var(--ant-color-text-secondary)',
                  marginLeft: 4,
                  marginRight: 0,
                }}
              >
                {pinEnabled ? '需要PIN码验证' : '无需PIN码验证'}
              </span>
            </div>
            <Button className="my-btn" type="primary" size="small" onClick={() => showPinModal(pinEnabled ? 'disable' : 'enable')} loading={loading} style={{ marginLeft: 'auto' }}>
              {pinEnabled ? '关闭PIN码' : '启用PIN码'}
            </Button>
          </div>
        </Descriptions.Item>
        <Descriptions.Item label="飞行模式">
          <div>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <Switch
                checked={airplane}
                loading={loading}
                onChange={handleAirplane}
                style={{ marginRight: 8 }}
              />
              <span style={{ color: airplane ? 'var(--ant-color-primary)' : 'var(--ant-color-text-secondary)', marginRight: 16 }}>{airplane ? '已开启' : '已关闭'}</span>
            </div>
            <div className="my-tip">
              开启飞行模式将断开所有网络
            </div>
          </div>
        </Descriptions.Item>
      </Descriptions>
      <Modal
        open={pinModal}
        title={pinMode === 'enable' ? '启用PIN码' : '关闭PIN码'}
        onCancel={() => setPinModal(false)}
        onOk={handlePinOk}
        okText="确认"
        cancelText="取消"
        confirmLoading={loading}
      >
        <div style={{ marginBottom: 8 }}>
          {pinMode === 'enable' ? '启用PIN码后，每次开机都需要验证' : '关闭PIN码后，开机无需验证'}
        </div>
        <Input.Password
          placeholder="请输入PIN码"
          value={pin1}
          onChange={e => setPin1(e.target.value)}
          maxLength={8}
          style={{ marginBottom: 8 }}
        />
        {pinMode === 'enable' && (
          <Input.Password
            placeholder="请再次输入PIN码"
            value={pin2}
            onChange={e => setPin2(e.target.value)}
            maxLength={8}
            style={{ marginBottom: 8 }}
          />
        )}
        <div style={{ background: 'var(--ant-color-warning-bg)', border: '1px solid var(--ant-color-warning-border)', color: 'var(--ant-color-warning)', borderRadius: 4, padding: 8, marginBottom: 8 }}>
          <b>重要提示</b>
          <ul style={{ margin: 0, paddingLeft: 20, listStyleType: 'disc' }}>
            <li>PIN码必须为4-8位数字</li>
          </ul>
        </div>
        {pinError && <div style={{ color: 'red', marginBottom: 8 }}>{pinError}</div>}
      </Modal>
    </Card>
  );
};

const DeviceControlCard = () => {
  const { ip, port } = useWebSocketConfig();
  // driverList类型声明允许有enabled属性
  const [driverList, setDriverList] = useState<{ label: string; value: string; enabled?: boolean }[]>([]);
  const [selectedDriver, setSelectedDriver] = useState<string>('');
  const [perfMode, setPerfMode] = useState(false);
  const [loading, setLoading] = useState(false);
  const { message } = App.useApp();

  function isWsReady() {
    return wsService.getStatus && wsService.getStatus() === 'open';
  }

  // 查询所有状态
  const queryAll = async () => {
    if (!isWsReady()) {
      console.log('DeviceControlCard queryAll: ws 未连接');
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const ethResp = await safeSendAT(getEthDriverCmd(realGroup));
      const drvList = parseEthDriverList(ethResp, realGroup);
      // 优先选中已启用的网卡
      const enabledDrv = drvList.find(d => d.enabled);
      setDriverList(drvList);
      setSelectedDriver(enabledDrv ? enabledDrv.value : (drvList[0]?.value || ''));
      const perfResp = await safeSendAT(getPerfModeCmd(realGroup));
      setPerfMode(parsePerfMode(perfResp, realGroup));
    } catch (e) {
      setDriverList([]);
      setSelectedDriver('');
      setPerfMode(false);
      message.error('AT命令查询失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    let cancel = false;
    const doQuery = () => { if (!cancel) queryAll(); };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ip, port]);

  // 设置网卡驱动
  const handleSetDriver = async (drv: string) => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setEthDriverCmd(realGroup, drv));
      await queryAll();
      message.success('设置网卡驱动成功');
    } catch (e) {
      message.error('设置网卡驱动失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // 设置性能模式
  const handleSetPerf = async (on: boolean) => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setPerfModeCmd(realGroup, on));
      await queryAll();
      message.success('设置性能模式成功');
    } catch (e) {
      message.error('设置性能模式失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // 恢复出厂
  const handleReset = async () => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(getResetFactoryCmd(realGroup));
      message.success('恢复出厂设置命令已发送');
    } catch (e) {
      message.error('恢复出厂设置失败: ' + e);
    } finally {
      setLoading(false);
    }
  };
  // 重启
  const handleReboot = async () => {
    if (loading) return;
    if (!isWsReady()) {
      message.warning('网络未连接，请稍后重试');
      return;
    }
    setLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(getRebootCmd(realGroup));
      message.success('重启命令已发送');
    } catch (e) {
      message.error('重启失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card title="设备控制" className="my-card">
      <Descriptions bordered column={1} styles={{ label: { width: '30%' }, content: { fontSize: 14 } }}>
        <Descriptions.Item label={<span>网卡速率</span>}>
          <div style={{ marginBottom: 8 }}>
            <span style={{ display: 'flex', alignItems: 'center' }}>
              <span style={{ marginBottom: 0, fontWeight: 'bold', fontSize: 14 }}>当前配置：</span>
              <span style={{ color: 'var(--ant-color-success)', fontWeight: 'bold' }}>
                {driverList.find(d => d.value === selectedDriver)?.label || '-'}
              </span>
            </span>
            <div className="my-tip">
              修改网卡速率需要重启设备才生效
            </div>
          </div>
          <Select
            style={{ width: 240, marginTop: 8 }}
            value={selectedDriver || (driverList[0]?.value || undefined)}
            loading={loading}
            onChange={handleSetDriver}
            options={driverList}
            placeholder="请选择网卡驱动"
            disabled={loading || !driverList.length}
          />
        </Descriptions.Item>
        <Descriptions.Item label="性能模式">
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <Switch
              checked={perfMode}
              loading={loading}
              onChange={handleSetPerf}
              style={{ marginRight: 8 }}
            />
            <span style={{ color: perfMode ? 'var(--ant-color-primary)' : 'var(--ant-color-text-secondary)', marginRight: 16 }}>{perfMode ? '已开启' : '已关闭'}</span>
          </div>
          {/* <div style={{ color: '#888', fontSize: 13, marginTop: 4 }}>也许是MT5700M-CN的性能模式</div> */}
        </Descriptions.Item>
        <Descriptions.Item label="恢复出厂设置">
          <Button danger type="primary" onClick={handleReset} loading={loading} style={{ width: 140 }}>恢复出厂设置</Button>
        </Descriptions.Item>
        <Descriptions.Item label="模组重启">
          <Button danger type="primary" onClick={handleReboot} loading={loading} style={{ width: 140 }}>重 启</Button>
        </Descriptions.Item>
      </Descriptions>
    </Card>
  );
};

const DmzConfigCard = () => {
  const { ip, port } = useWebSocketConfig();
  // const group = getRememberedCommandGroup(ip, port) || 'Quectel_AT';
  const [status, setStatus] = useState<'loading' | 'unset' | 'set' | 'error'>('loading');
  const [dmzIp, setDmzIp] = useState('');
  const [dmzIpv6, setDmzIpv6] = useState('');
  const [inputAddr, setInputAddr] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const fetchStatus = async () => {
    setStatus('loading');
    setError('');
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const resp = await safeSendAT(getDmzStatusCmd(realGroup));
      const parsed = parseDmzStatus(resp, realGroup);
      if (parsed.ipv4.enabled && parsed.ipv4.ip) {
        setStatus('set');
        setDmzIp(parsed.ipv4.ip);
        setInputAddr(parsed.ipv4.ip);
      } else if (parsed.ipv6.enabled && parsed.ipv6.ip) {
        setStatus('set');
        setDmzIpv6(parsed.ipv6.ip);
        setInputAddr(parsed.ipv6.ip);
      } else {
        setStatus('unset');
        setDmzIp('');
        setDmzIpv6('');
        setInputAddr('');
      }
    } catch (e) {
      setStatus('error');
      setError('查询失败: ' + e);
    }
  };

  useEffect(() => {
    let cancel = false;
    const doQuery = () => { if (!cancel) fetchStatus(); };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ip, port]);

  function isIpv4(addr: string) {
    return /^\d{1,3}(\.\d{1,3}){3}$/.test(addr);
  }
  function isIpv6(addr: string) {
    return /^([\da-fA-F]{0,4}:){2,7}[\da-fA-F]{0,4}$/.test(addr);
  }

  const handleApply = async () => {
    setLoading(true);
    setError('');
    try {
      if (isIpv4(inputAddr)) {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(setDmzIpv4Cmd(realGroup, inputAddr));
      } else if (isIpv6(inputAddr)) {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(setDmzIpv6Cmd(realGroup, inputAddr));
      } else {
        setError('请输入合法的IPv4或IPv6地址');
        setLoading(false);
        return;
      }
      await fetchStatus();
    } catch (e) {
      setError('设置失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  const handleDisable = async () => {
    setLoading(true);
    setError('');
    try {
      if (dmzIp) {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(disableDmzIpv4Cmd(realGroup));
      } else if (dmzIpv6) {
        await ensureGroupReady(ip, port);
        const realGroup = await getCurrentGroupAsync(ip, port);
        await safeSendAT(disableDmzIpv6Cmd(realGroup));
      } else {
        setError('当前无可禁用的DMZ主机');
        setLoading(false);
        return;
      }
      await fetchStatus();
    } catch (e) {
      setError('禁用失败: ' + e);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card title="DMZ主机设置" extra={<Button className="my-btn" onClick={fetchStatus} loading={status === 'loading'} icon={<ReloadOutlined />}>刷新</Button>} className="my-card">
      <div className="my-title">
        当前状态: {status === 'set'
          ? <span style={{ color: 'var(--ant-color-success)' }}>已配置 ({dmzIp || dmzIpv6})</span>
          : status === 'unset'
            ? <span style={{ color: 'var(--ant-color-text-secondary)' }}>未配置</span>
            : <span style={{ color: 'red' }}>异常</span>}
      </div>
      <div className="my-tip" style={{ marginBottom: 16 }}>
        DMZ主机将完全暴露在公网中，请谨慎配置
      </div>
      <Input
        placeholder="请输入DMZ主机IP (支持IPv4/IPv6)"
        value={inputAddr}
        onChange={e => setInputAddr(e.target.value)}
        disabled={loading}
        style={{ width: '70%', marginRight: 8 }}
      />
      <Button type="primary" onClick={handleApply} loading={loading} disabled={!inputAddr || loading} style={{ marginRight: 8 }}>应用</Button>
      <Button danger onClick={handleDisable} loading={loading} disabled={status !== 'set' || loading}>禁用</Button>
      {error && <div style={{ color: 'red', marginTop: 8 }}>{error}</div>}
    </Card>
  );
};

const NetSystemConfigCard = () => {
  const { ip, port } = useWebSocketConfig();
  // const group = getRememberedCommandGroup(ip, port) || 'Quectel_AT';
  const [ratOrder, setRatOrder] = useState<string[]>([]);
  const [ratOrderLoading, setRatOrderLoading] = useState(false);
  const [roamPref, setRoamPref] = useState(false);
  const [roamLoading, setRoamLoading] = useState(false);
  const [srvDomain, setSrvDomain] = useState<number>(2);
  const [srvLoading, setSrvLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  // 新增：保存初始配置用于脏数据检测
  const [initialRatOrder, setInitialRatOrder] = useState<string[]>([]);
  const [initialRoamPref, setInitialRoamPref] = useState(false);
  const [initialSrvDomain, setInitialSrvDomain] = useState<number>(2);
  const { message } = App.useApp();

  const RAT_OPTIONS = [
    { label: '5G', value: 'NR5G' },
    { label: '4G', value: 'LTE' },
    { label: '3G', value: 'WCDMA' },
  ];
  const ROAM_OPTIONS = [
    { label: '仅使用本地网络', value: false },
    { label: '允许使用漫游网络(可能产生额外费用)', value: true },
  ];
  const SRV_OPTIONS = [
    { label: '仅支持通话功能', value: 0 },
    { label: '仅支持上网功能', value: 1 },
    { label: '同时支持通话和上网', value: 2 },
  ];

  // 查询
  const fetchAll = async () => {
    setRatOrderLoading(true); setRoamLoading(true); setSrvLoading(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const ratResp = await safeSendAT(getRatOrderCmd(realGroup));
      const parsedRat = parseRatOrder(ratResp, realGroup);
      setRatOrder(parsedRat);
      setInitialRatOrder(parsedRat);
    } catch { setRatOrder([]); setInitialRatOrder([]); }
    setRatOrderLoading(false);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const roamResp = await safeSendAT(getRoamPrefCmd(realGroup));
      const parsedRoam = parseRoamPref(roamResp, realGroup);
      setRoamPref(parsedRoam);
      setInitialRoamPref(parsedRoam);
    } catch { setRoamPref(false); setInitialRoamPref(false); }
    setRoamLoading(false);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      const srvResp = await safeSendAT(getSrvDomainCmd(realGroup));
      const parsedSrv = parseSrvDomain(srvResp, realGroup);
      setSrvDomain(parsedSrv);
      setInitialSrvDomain(parsedSrv);
    } catch { setSrvDomain(2); setInitialSrvDomain(2); }
    setSrvLoading(false);
  };

  useEffect(() => {
    let cancel = false;
    const doQuery = () => { if (!cancel) fetchAll(); };
    if (wsService.getStatus && wsService.getStatus() === 'open') {
      doQuery();
    } else {
      wsService.addOnOpenCallback(doQuery);
    }
    return () => {
      cancel = true;
      wsService.removeOnOpenCallback(doQuery);
    };
  }, [ip, port]);

  // 判断是否有变更
  const isDirty =
    JSON.stringify(ratOrder) !== JSON.stringify(initialRatOrder) ||
    roamPref !== initialRoamPref ||
    srvDomain !== initialSrvDomain;

  // 统一保存
  const handleSaveAll = async () => {
    setSaving(true);
    try {
      await ensureGroupReady(ip, port);
      const realGroup = await getCurrentGroupAsync(ip, port);
      await safeSendAT(setRatOrderCmd(realGroup, ratOrder));
      await safeSendAT(setRoamPrefCmd(realGroup, roamPref));
      await safeSendAT(setSrvDomainCmd(realGroup, srvDomain));
      message.success('网络系统配置已保存');
      // 保存后刷新初始值
      setInitialRatOrder(ratOrder);
      setInitialRoamPref(roamPref);
      setInitialSrvDomain(srvDomain);
    } catch (e) {
      message.error('保存失败: ' + e);
    } finally {
      setSaving(false);
    }
  };

  return (
    <Card title="网络系统配置" extra={<Button className="my-btn" onClick={fetchAll} loading={ratOrderLoading || roamLoading || srvLoading} icon={<ReloadOutlined />}>刷新</Button>} className="my-card">
      <div style={{ padding: 16 }}>
        <Row gutter={24}>
          <Col xs={24} md={12}>
            <div className="my-title" style={{ marginBottom: 8 }}>网络制式优先级</div>
            <div style={{ marginBottom: 8, color: 'var(--ant-color-text-secondary)' }}>{ratOrder.map(r => RAT_OPTIONS.find(o => o.value === r)?.label).filter(Boolean).join(' -> ') || '-'}</div>
            <Select
              mode="multiple"
              allowClear={false}
              style={{ width: '100%' }}
              value={ratOrder}
              onChange={setRatOrder}
              options={RAT_OPTIONS}
              placeholder="请选择优先级"
              loading={ratOrderLoading}
              maxTagCount={3}
            />
          </Col>
          <Col xs={24} md={12}>
            <div className="my-title" style={{ marginBottom: 8 }}>漫游设置</div>
            <div style={{ marginBottom: 8, color: 'var(--ant-color-text-secondary)' }}>{ROAM_OPTIONS.find(o => o.value === roamPref)?.label}</div>
            <Select
              style={{ width: '100%' }}
              value={roamPref}
              onChange={setRoamPref}
              options={ROAM_OPTIONS}
              loading={roamLoading}
            />
          </Col>
        </Row>
        <Row gutter={24} style={{ marginTop: 24 }}>
          <Col xs={24} md={12}>
            <div className="my-title" style={{ marginBottom: 8 }}>服务类型</div>
            <div style={{ marginBottom: 8, color: 'var(--ant-color-text-secondary)' }}>{SRV_OPTIONS.find(o => o.value === srvDomain)?.label}</div>
            <Select
              style={{ width: '100%' }}
              value={srvDomain}
              onChange={setSrvDomain}
              options={SRV_OPTIONS}
              loading={srvLoading}
            />
          </Col>
        </Row>
        <div style={{ marginTop: 32 }}>
          <Button type="primary" block style={{ width: '100%' }} onClick={handleSaveAll} loading={saving} disabled={!isDirty || saving}>应用配置</Button>
        </div>
      </div>
    </Card>
  );
};

const InfoRow = ({ label, value }: { label: string; value: string }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', color: 'var(--ant-color-text)', fontSize: 15 }}>
    <span style={{ color: 'var(--ant-color-text-secondary)' }}>{label}</span>
    <span>{value}</span>
  </div>
);